.navbar {
   position: sticky;
  box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0px;
    background-color: #f3c4ae;
    width: 100%;
    height: 120px;
    padding: 30px;
  }
  
  .navbar>a {
    font-size: larger;
    margin: 80px;
    margin-right: 5px;
    text-decoration: none;
    color: rgb(134, 115, 115);
    display: flex;
  }
  
  .navbar>a:hover {
    text-decoration: underline;
  }

  .navbar>a:global.active {
    font-size:xx-large;
text-decoration: underline;
color: rgb(96, 73, 56);
  }

  @media screen and (max-width: 600px) {
    .navbar {
      flex-direction: column; 
      align-content: flex-start;
    }
    .navbar a {
      margin-top: 5px;
    }
  }